// src/components/Projects.js
import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import './ProjectStyles.css';
import { firestore } from './firebaseConfig';
import { collection, query, orderBy, getDocs } from 'firebase/firestore';

const Projects = () => {
	const [projects, setProjects] = useState([]);

	const history = useHistory();

	useEffect(() => {
		const fetchProjects = async () => {
			const projectsRef = collection(firestore, 'project_info');
			const q = query(projectsRef, orderBy('created_at', 'desc')); // Order by 'created_at' in descending order

			try {
				const querySnapshot = await getDocs(q);
				const projectsData = querySnapshot.docs.map((doc) => ({
					id: doc.id,
					...doc.data(),
				}));
				setProjects(projectsData);
			} catch (error) {
				console.error('Error fetching projects:', error);
			}
		};

		fetchProjects();
	}, []);

	// Define a style object for the table
	const tableStyle = {
		borderCollapse: 'collapse',
		width: '100%',
	};
	const table = {
		padding: '60px',
	};
	const navigateToAboutMe = () => {
		history.push('/aboutme');
	};
	const cellStyle = {
		border: '1px solid black',
		padding: '8px',
	};

	return (
		<div style={table}>
			<h1>My Projects</h1>

			<div className='projects-header'>
				<button onClick={navigateToAboutMe} className='about-me-button'>
					About Me
				</button>
			</div>
			<table style={tableStyle}>
				<thead>
					<tr>
						<th style={cellStyle}>Project Name</th>
						<th style={cellStyle}>Project Link</th>
						<th style={cellStyle}>Project Summary</th>
						<th style={cellStyle}>Technologies Used</th>
					</tr>
				</thead>
				<tbody>
					{projects.map((project) => (
						<tr key={project.id}>
							<td style={cellStyle}>{project.project_name}</td>
							<td style={cellStyle}>
								<a
									href={project.project_link}
									target='_blank'
									rel='noopener noreferrer'>
									View Demo
								</a>
							</td>
							<td style={cellStyle}>{project.project_summary}</td>
							<td style={cellStyle}>{project.technologies_used}</td>
						</tr>
					))}
				</tbody>
			</table>
		</div>
	);
};

export default Projects;
