// src/components/AddProject.js
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { firestore } from './firebaseConfig';
import { collection, addDoc } from 'firebase/firestore';
import './AddProjectStyles.css'; // Assuming you have this CSS file

const AddProject = () => {
	const [projectName, setProjectName] = useState('');
	const [projectLink, setProjectLink] = useState('');
	const [technologiesUsed, setTechnologiesUsed] = useState('');
	const [projectSummary, setProjectSummary] = useState('');
	const history = useHistory();

	const handleSubmit = async (e) => {
		e.preventDefault();

		try {
			// Add a new document in collection "project_info"
			await addDoc(collection(firestore, 'project_info'), {
				project_name: projectName,
				project_link: projectLink,
				technologies_used: technologiesUsed,
				project_summary: projectSummary,
				created_at: new Date(), // Timestamp for when the project is added
			});

			alert('Project added successfully!');
			history.push('/'); // Redirect to the homepage
		} catch (error) {
			console.error('Error adding project:', error);
			alert('Error adding project');
		}
	};

	return (
		<form onSubmit={handleSubmit} className='add-project-form'>
			<label>
				Project Name:
				<input
					type='text'
					value={projectName}
					onChange={(e) => setProjectName(e.target.value)}
				/>
			</label>
			<label>
				Project Link:
				<input
					type='text'
					value={projectLink}
					onChange={(e) => setProjectLink(e.target.value)}
				/>
			</label>
			<label>
				Project Summary:
				<input
					type='text'
					value={projectSummary}
					onChange={(e) => setProjectSummary(e.target.value)}
				/>
			</label>
			<label>
				Technologies Used:
				<input
					type='text'
					value={technologiesUsed}
					onChange={(e) => setTechnologiesUsed(e.target.value)}
				/>
			</label>
			<button type='submit'>Add Project</button>
		</form>
	);
};

export default AddProject;
